// пульсація
#heart {
  position: relative;
  margin: 0 auto;
  animation: transformHeart 1.5s ease-in-out infinite;
}

@keyframes transformHeart {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.5, 1.5);
  }
  100% {
    transform: scale(1, 1);
  }
}
